import React, {useState, useEffect} from 'react'
import { AiFillFolderAdd } from "react-icons/ai";
import Toast from '../notification/Toast';
import DateSelector from '../datepicker/DateSelector';

const AddTaskModal = ({closeModal}) => {  
  const [taskTitle, setTaskTitle] = useState("")
  const [taskDescription, setTaskDescription] = useState("")
  const [alertMessage, setAlertMessage] = useState("")

  const handleTaskTitle = (e) => {    
    setTaskTitle( e.currentTarget.value);
  };

  const handleTaskDescription = (e) => {    
    setTaskDescription( e.currentTarget.value);
  };


  const submitNewTask = () => {

  }
  
  return (
    <>
      <div style={{ display: "flex" }}>           
        <div
          style={{
            position: "absolute",
            height: "100vh",
            width: "100%",
            backgroundColor: "rgba(0,0,0,0.8)",       
            zIndex: "999"
          }}
        >
          <div
            style={{
              position: "relative",
              height: "100vh",
              width: "100%",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <div className="popUp-modal "style ={{width: '40rem', minHeight: '20rem'}}>
              <button className='close-modal-btn' onClick={closeModal}>X</button>
              {/* <form action="categories/create" method="post" className='flex-column' > */}
              <div  className='flex-column' style={{width: '100%'}}>
                <div>Create New Task</div>   
                <DateSelector purpose='Deadline'/>
                <input                 
                  name="task_title"               
                  className="modal-input flex-row"
                  placeholder='Task Title'
                  onChange={handleTaskTitle}
                  value={taskTitle}
                  required>                  
                </input>
                <textarea
                  name="task_description"
                  className="modal-input flex-row focus:outline-0 "
                  placeholder='Task Description'
                  onChange={handleTaskDescription}
                  value={taskDescription}
                  rows={5}
                  style={{resize: "none"}}
                />                  
                <button className='modal-submit' onClick={submitNewTask}>Save Task</button>
              </div>
              {/* </form> */}
            </div>
          </div>
        </div>
    
      </div>
      <Toast toastMsg={alertMessage}/>
    </>
  )
}

const NewTaskButton = () => {
  const [showAddTask, setShowAddCTask] = useState(false)

  const handleTask = () => {
    setShowAddCTask(!showAddTask)
  }

  const closeModal = () => {
    setShowAddCTask(false)  
  }
 
  return (
    <>
      {showAddTask && <AddTaskModal closeModal={closeModal}/>}   
      <div className='flex-row create-task-btn' onClick={handleTask}>   
         
        <div>
          <AiFillFolderAdd fontSize="2rem" color=''/>
        </div>       
        <div>
          Create New Task
        </div>
      </div>
    </>
  )

}

const Tasks = () => {
  return (   
    <>
      <NewTaskButton />
    </>
  )
}

export default Tasks