import React, {useEffect, useState} from 'react'


import axios from 'axios'

const SidePanel = () => {
  const [userCategories, setUserCategories] = useState([])
  const [categoryFilter, setCategoryFilter] = useState("");

  useEffect(() => {
    myCategories()  
    return () => {}
  }, [])

  const myCategories  = async () => {
    // const { data } = await axios.get("/categories/all");//production
    const { data } = await axios.get(" http://localhost:3000/categories/all");   
    await data && setUserCategories(data)
  };

  const handleCategoryFilter = (e) => {
    setCategoryFilter(e.currentTarget.value);
  }
  return (
    <div className="side-panel">
      <input
        className='panel-input'
        placeholder='Search Category'
        value={categoryFilter}
        onChange={handleCategoryFilter}
      ></input>
       {/* .toLowerCase() */}
      <div className='panel-display' >
        {userCategories
          .filter(item => item.title.toLowerCase()
          .includes(categoryFilter.toLowerCase())) 
          .map((categories, idx) => {
          return (
            <div key={idx} className="panel-items" data-categoryID={categories.id}> {categories.title}</div>
          )
        })}
      </div>  
    </div>

  )
}

export default SidePanel